import Util from "../../../Tools/Util";

export default class UIItemDay {
    private node: cc.Node;
    private index: number;
    private day: number;
    private cb: (index, day) => void;
    private lbDay: cc.Label;
    private spSel: cc.Sprite;
    public Init(node: cc.Node, idex, day, sel, cb: (index, day) => void) {
        this.node = node;
        this.index = idex;
        this.day = day;
        this.cb = cb;
        this.lbDay = this.node.getChildByName("lbDay").getComponent(cc.Label);
        this.lbDay.string = day;
        //this.spSel.enabled = sel;
        Util.SetClickAction(this.node.getComponent(cc.Button), () => {
            this.cb(this.index, this.day)
        })
    }
}